@import '../vars' ;

.managerforicomoon-modal--container {
    max-width: 900px;

    h1 {
        color: $color-main;
    }
    h2 {
        flex-basis: 100%;
        margin: 0.2rem;
    }
}

.managerforicomoon-modal--options {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
    border: 1px dashed #ccc ;
    padding: 10px;
    gap: 10px;
    
    .managerforicomoon-modal--option {
        max-width: 400px;
        width: 50%;
        display: flex;
        align-items: center;
        position: relative;
        label {
            flex-basis: 150px;
            flex-shrink: 0;
        }
    }
    .managerforicomoon-modal--option-wide {
        width: 100%;
        max-width: none;
    }
    .wp-picker-container {
        display: inline-block;
        
    }
    .wp-picker-active {
        position: absolute;
        left: 144px;
        top: -12px;
        z-index: 900;
        border: 1px solid #ccc;
        padding: 5px ;
        background-color: #fff;

        .iris-border {
            border-width: 0;
        }
    }
    input {
        width: 100%;
    }
}  
@media (max-width: 896px) {
    .managerforicomoon-modal--options {
        .managerforicomoon-modal--option {
            width: 100%;
            max-width: none;
        }
    }
}

.managerforicomoon-modal--icons-list {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 8px;
    margin: 20px;
 
    .managerforicomoon-modal--icon { 
        width: 50px;
        height: 50px;
        font-size: 20px;
        line-height: 50px;
        cursor: pointer;
        background-color: transparent;
        border: 1px solid #ccc;
        @extend %transition;
        overflow: hidden;
        
        &:hover {
            border-style: solid;
            border-color: $color-main;
            color: $color-main;
            background-color: $color-main-light; 
        }
        &-active {
            border-style: solid;
            border-color: $color-main;
            color: #1d2327;
            background-color: $color-main-light;
        }
    }
} 
